	module.exports = {"pathContext":{"posts":[{"html":"<p>A container is a set of one or more applications isolated from the rest of a system. It's a mechanism originated from the necesity of sharing server resources like memory and CPU amongst different apps.</p>\n<p>The previously used model of Hypervisors, popularly known as virtual machines would require each application to have its own operational system, which is suboptimal as it incurs in monetary and operational costs such as licensing and hardware upgrades, as operational systems require the consumption of resources for its own sustaining.</p>\n<p>Instead, containers sit on top of and share the same operational system as all others in the machine.</p>\n<p><a href=\"https://www.docker.com/sites/default/files/d8/2018-11/docker-containerized-and-vm-transparent-bg.png\">Illustration on Container Model from Docker Website</a></p>","id":"/Users/zavareze/projects/zavjsblog/src/pages/2019-02-02-what-is-a-container/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"Sa, 02/19","path":"/what-is-a-container","title":"What is a container?","excerpt":"","tags":["container","linux"]}}],"tagName":"linux"}}

